@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap);
body {
    margin: 0;
    font-family: 'Jost' sans-serif;
    /* font-family: 'Jost', -apple-system, BlinkMacSystemFont, 'Roboto' sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    font-family: 'Jost' sans-serif;
}

.active {
    background-color: rgba(18, 69, 66, 0.4) !important;
    border: 1px solid rgba(50, 50, 50, 0.3) !important;
    color: #fff !important;
}

